const testimonials: Testimonial[] = [
  {
    id: 1,
    text: '–û—Ç–ª–∏—á–Ω–∞—è –∫–æ–º–∞–Ω–¥–∞ –ø—Ä–æ—Ñ–µ—Å—Å–∏–æ–Ω–∞–ª–æ–≤! –°–¥–µ–ª–∞–ª–∏ —Å–∞–π—Ç —Ç–æ—á–Ω–æ –≤ —Å—Ä–æ–∫ –∏ –¥–∞–∂–µ –ª—É—á—à–µ, —á–µ–º –º—ã –æ–∂–∏–¥–∞–ª–∏. –†–µ–∫–æ–º–µ–Ω–¥—É—é –≤—Å–µ–º!',
    author: '–ê–ª–µ–∫—Å–µ–π –ö–æ–∑–ª–æ–≤',
    position: 'CEO, TechCorp',
    initials: '–ê–ö',
  },
  {
    id: 2,
    text: '–†–∞–±–æ—Ç–∞–µ–º —Å TechStart —É–∂–µ —Ç—Ä–µ—Ç–∏–π –≥–æ–¥. –í—Å–µ–≥–¥–∞ –∫–∞—á–µ—Å—Ç–≤–µ–Ω–Ω–æ, –±—ã—Å—Ç—Ä–æ –∏ —Å –ø–æ–Ω–∏–º–∞–Ω–∏–µ–º –Ω–∞—à–∏—Ö –ø–æ—Ç—Ä–µ–±–Ω–æ—Å—Ç–µ–π.',
    author: '–ú–∞—Ä–∏—è –°–º–∏—Ä–Ω–æ–≤–∞',
    position: '–î–∏—Ä–µ–∫—Ç–æ—Ä, StyleShop',
    initials: '–ú–°',
  },
  {
    id: 3,
    text: '–ë–ª–∞–≥–æ–¥–∞—Ä—è –Ω–æ–≤–æ–º—É —Å–∞–π—Ç—É –Ω–∞—à–∏ –ø—Ä–æ–¥–∞–∂–∏ –≤—ã—Ä–æ—Å–ª–∏ –Ω–∞ 40%. –°–ø–∞—Å–∏–±–æ –∫–æ–º–∞–Ω–¥–µ –∑–∞ –ø—Ä–æ—Ñ–µ—Å—Å–∏–æ–Ω–∞–ª—å–Ω—ã–π –ø–æ–¥—Ö–æ–¥!',
    author: '–î–º–∏—Ç—Ä–∏–π –ü–µ—Ç—Ä–æ–≤',
    position: '–í–ª–∞–¥–µ–ª–µ—Ü, FoodDelivery',
    initials: '–î–ü',
  },
];

export const Testimonials: FC = () => {
  const sectionRef = useRef<HTMLElement>(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.1 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  return (
    <section id="testimonials" className={styles.testimonials} ref={sectionRef}>
      <div className={styles.container}>
        <div className={`${styles.header} ${isVisible ? styles.visible : ''}`}>
          <h2 className={styles.title}>–û—Ç–∑—ã–≤—ã –∫–ª–∏–µ–Ω—Ç–æ–≤</h2>
          <p className={styles.subtitle}>
            –ß—Ç–æ –≥–æ–≤–æ—Ä—è—Ç –æ –Ω–∞—Å –Ω–∞—à–∏ –∫–ª–∏–µ–Ω—Ç—ã
          </p>
        </div>

        <div className={styles.grid}>
          {testimonials.map((testimonial, index) => (
            <div
              key={testimonial.id}
              className={`${styles.card} ${isVisible ? styles.visible : ''}`}
              style={{ animationDelay: `${index * 150}ms` }}
            >
              <div className={styles.quote}>"</div>
              <p className={styles.text}>{testimonial.text}</p>
              <div className={styles.author}>
                <div className={styles.avatar}>
                  {testimonial.initials}
                </div>
                <div className={styles.authorInfo}>
                  <h4 className={styles.authorName}>{testimonial.author}</h4>
                  <span className={styles.authorPosition}>{testimonial.position}</span>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Testimonials;
