export const Portfolio: FC = () => {
  const sectionRef = useRef<HTMLElement>(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.1 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  return (
    <section id="portfolio" className={styles.portfolio} ref={sectionRef}>
      <div className={styles.container}>
        <div className={`${styles.header} ${isVisible ? styles.visible : ''}`}>
          <h2 className={styles.title}>–ù–∞—à–∏ —Ä–∞–±–æ—Ç—ã</h2>
          <p className={styles.subtitle}>
            –ü—Ä–æ–µ–∫—Ç—ã, –∫–æ—Ç–æ—Ä—ã–º–∏ –º—ã –≥–æ—Ä–¥–∏–º—Å—è
          </p>
        </div>

        <div className={styles.grid}>
          {projects.map((project, index) => (
            <div
              key={project.id}
              className={`${styles.item} ${isVisible ? styles.visible : ''}`}
              style={{ 
                animationDelay: `${index * 100}ms`,
                background: project.color,
              }}
            >
              <div className={styles.overlay}>
                <h3 className={styles.itemTitle}>{project.title}</h3>
                <p className={styles.itemCategory}>{project.category}</p>
                <button className={styles.viewButton}>
                  –°–º–æ—Ç—Ä–µ—Ç—å –ø—Ä–æ–µ–∫—Ç
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Portfolio;
