import React, { FC, useRef, useEffect, useState } from 'react';
import styles from './Services.module.css';

interface Service {
  title: string;
  description: string;
  price: string;
  period?: string;
}

const services: Service[] = [
  {
    title: '–í–µ–±-—Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∞',
    description: '–°–æ–∑–¥–∞–Ω–∏–µ —Å–æ–≤—Ä–µ–º–µ–Ω–Ω—ã—Ö —Å–∞–π—Ç–æ–≤ –∏ –≤–µ–±-–ø—Ä–∏–ª–æ–∂–µ–Ω–∏–π –ª—é–±–æ–π —Å–ª–æ–∂–Ω–æ—Å—Ç–∏',
    price: '–æ—Ç 50 000 ‚ÇΩ',
  },
  {
    title: '–ú–æ–±–∏–ª—å–Ω—ã–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è',
    description: '–†–∞–∑—Ä–∞–±–æ—Ç–∫–∞ –Ω–∞—Ç–∏–≤–Ω—ã—Ö –∏ –∫—Ä–æ—Å—Å–ø–ª–∞—Ç—Ñ–æ—Ä–º–µ–Ω–Ω—ã—Ö –º–æ–±–∏–ª—å–Ω—ã—Ö –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–π',
    price: '–æ—Ç 150 000 ‚ÇΩ',
  },
  {
    title: 'UI/UX –î–∏–∑–∞–π–Ω',
    description: '–ü—Ä–æ–µ–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —É–¥–æ–±–Ω—ã—Ö –∏ –∫—Ä–∞—Å–∏–≤—ã—Ö –∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å–æ–≤ –¥–ª—è –≤–∞—à–∏—Ö –ø—Ä–æ–¥—É–∫—Ç–æ–≤',
    price: '–æ—Ç 30 000 ‚ÇΩ',
  },
  {
    title: 'SEO –ø—Ä–æ–¥–≤–∏–∂–µ–Ω–∏–µ',
    description: '–ö–æ–º–ø–ª–µ–∫—Å–Ω–æ–µ –ø—Ä–æ–¥–≤–∏–∂–µ–Ω–∏–µ —Å–∞–π—Ç–æ–≤ –≤ –ø–æ–∏—Å–∫–æ–≤—ã—Ö —Å–∏—Å—Ç–µ–º–∞—Ö',
    price: '–æ—Ç 25 000 ‚ÇΩ',
    period: '/–º–µ—Å',
  },
];

export const Services: FC = () => {
  const sectionRef = useRef<HTMLElement>(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.1 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  return (
    <section id="services" className={styles.services} ref={sectionRef}>
      <div className={styles.container}>
        <div className={`${styles.header} ${isVisible ? styles.visible : ''}`}>
          <h2 className={styles.title}>–ù–∞—à–∏ —É—Å–ª—É–≥–∏</h2>
          <p className={styles.subtitle}>
            –ü–æ–ª–Ω—ã–π —Å–ø–µ–∫—Ç—Ä —É—Å–ª—É–≥ –¥–ª—è –≤–∞—à–µ–≥–æ —Ü–∏—Ñ—Ä–æ–≤–æ–≥–æ –ø—Ä–∏—Å—É—Ç—Å—Ç–≤–∏—è
          </p>
        </div>

        <div className={styles.grid}>
          {services.map((service, index) => (
            <div
              key={service.title}
              className={`${styles.card} ${isVisible ? styles.visible : ''}`}
              style={{ animationDelay: `${index * 100}ms` }}
            >
              <h3 className={styles.cardTitle}>{service.title}</h3>
              <p className={styles.cardDescription}>{service.description}</p>
              <div className={styles.price}>
                {service.price}
                {service.period && <span className={styles.period}>{service.period}</span>}
              </div>
              <button className={styles.cardButton}>–ü–æ–¥—Ä–æ–±–Ω–µ–µ</button>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Services;
